import tuiIcon from "@/components/icon/icon"
import {
	ajax,
	getSecret
} from '@/common/ajax/ajax.js'
export default {
	components: {
		tuiIcon
	},
	data() {
		return {
			openid: '',
			// 商品分类ID
			fid: 160,
			mid: '',
			small: '',
			// pageid: 1,
			secret: '',
			timestamp: '',
			showCategoryIndex: 1000,
			headerPosition: "fixed",
			tabbar: [],
			winHeight: "", //窗口高度
			currentTab: 0, //预设当前项的值
			scrollLeft: 0, //tab标题的滚动条位置
			showHeader: true,
			//分类列表
			categoryList: [],
			productList: [],
			pagecount: '',
			// 显示全部分类
			allShow: false,
			stateTab: true,
			pronum: 0,
			loadingText: '正在加载中',
			scrollTop: 0,
			flag: true, //记录是否请求数据的状态
			page: 1,
			addShow: true,
			top: 0,
			newImg:null
		}
	},

	onPageScroll(e) {
		
		if (e.scrollTop >= 0) {
			this.headerPosition = "fixed";
		} else {
			this.headerPosition = "absolute";
		}
	},

	onLoad() {			
		this.getbig()
		if(this.showCategoryIndex==1000){
			this.getnewGoods()
		}else{
			this.getList()
		}
		
		
	},
	onShow() {
		let index = uni.getStorageSync('cateSelect');
		if (index) {
			this.productList = []
			this.showCategoryIndex = index;
			this.loadingText = '正在加载中...';
			if(index==1000){
				this.tabbar=[];
				this.getnewGoods()
			}else{
				this.fid = index;
				this.getList();
			}
		}
	},
	onHide() {
		uni.removeStorageSync('cateSelect');
	},		
	methods: {			
		loadMore() {
			this.page++
			if (this.page <= this.pagecount) {
				setTimeout(()=>{
					this.getgoodList();
				},2000)
				
				this.loadingText = '正在加载中...';
			} else {
				this.loadingText = '到底了';
			}
		},
		// 点击标题切换当前页时改变样式
		swichNav(e) {
			if (this.currentTab == e) {
				return false;
			} else {
				this.currentTab = e
			}
			this.mid = e;
			this.page = 1;
			this.productList = [];
			this.getgoodList();
		},
		isshow() {
			this.allShow = true
		},
		close() {
			this.allShow = false;
		},
		swichall() {
			this.mid = '';
			this.currentTab = 0;
			this.page = 1;
			this.productList = [];
			this.getgoodList();
		},
		// 获取分类商品
		getList() {
			let data = {
				fid: this.fid
			}
			this.$xm.post('/Procate/getMid', data, (res) => {
				this.tabbar = res;
				this.getgoodList()
			})

		},
		// 获取商品总分类
		getbig() {
			this.$xm.post('/Procate/getBig', '', (res) => {
				this.categoryList = res;
			})

		},
		// 获取商品
		getgoodList() {
			this.flag = false;
			let data = {
				big: this.fid,
				mid: this.mid,
				small: this.small,
				pageid: this.page
			}
			this.$xm.post('/Product/proList', data, (res) => {
				let result = res.prolist;
				if (res.prolist.length == 0 || res.count==1) {
					this.loadingText = '没有更多商品了！'
				} else {
					this.loadingText = '正在加载中...'
				}
				result.map((ele) => {
					ele.pro_sn = 'http://img.xmvogue.com/thumb/' + ele.pro_sn + '.jpg?x-oss-process=style/300'
					ele.itemcount = 0;

				})
				this.pagecount = res.count;
				this.productList = this.productList.concat(result);

				console.log(this.productList)
			})

		},
		//分类切换显示
		showCategory(index) {
			this.loadingText = '正在加载中...';
			this.productList = [];
			this.showCategoryIndex = index;
			this.fid = index;
			this.currentTab = 0;
			this.mid = '';
			this.page = 1;
			this.newImg=null;
			this.getList()
		},
		// 新品推荐
		shownewGoods(){
			this.loadingText = '正在加载中...';
			this.productList = [];
			this.showCategoryIndex = 1000;
			this.page = 1;
			this.tabbar=[];
			this.getnewGoods()
		},
		getnewGoods(){
			let data={
				p:this.page
			}
			this.$xm.post('/Index/newpro',data,(res)=>{
				console.log(res);
				this.pagecount=Number(res.p);
				if (this.pagecount==1) {
					this.loadingText = '到底了！'
				} else {
					this.loadingText = '正在加载中...'
				}
				res.data.forEach(ele=>{
					ele.pro_sn = 'http://img.xmvogue.com/thumb/' + ele.pro_sn + '.jpg?x-oss-process=style/300'
					ele.itemcount = 0;
				})
				this.productList = this.productList.concat(res.data);
				this.newImg=res.img;
				console.log(this.newImg);
			})
		},
		//商品跳转
		toGoods(e) {
			uni.navigateTo({
				url: '../../goods/goods?gid=' + e.id
			});
		},
		// 加入购物车
		joinCart(e) {
			if (this.addShow) {
				e.itemcount++;
			}
			let data = {
				proid: e.id,
				pronum: 1
			};
			this.$xm.post('/Cart/add', data, (res) => {
				let result = res.msg;
				if (res.s == 1) {
					this.addShow = true;
					uni.showToast({
						title: result
					});
				} else if (res.s == 0) {
					this.addShow = false;
					uni.showToast({
						title: result,
						icon: 'none'
					});
				}
			})
		},
		//搜索跳转
		toSearch() {
			uni.navigateTo({
				url: '../../search/search/search'
			})
		}
	}
}
